  const ColorButton = withStyles((theme) => ({
    root: {
      color: theme.palette.getContrastText('#17a1b9'),
      backgroundColor: '#17a1b9',
      '&:hover': {
        backgroundColor: '#17a1b9',
      },
    },
  }))(Button);
  
  const ColorButton1 = withStyles((theme) => ({
    root: {
      color: theme.palette.getContrastText("#ffc107"),
      backgroundColor: '#ffc107',
      '&:hover': {
        backgroundColor: "#ffc107",
      },
    },
  }))(Button);

  const ColorButton2 = withStyles((theme) => ({
    root: {
      color: theme.palette.getContrastText("#28a745"),
      backgroundColor: '#28a745',
      '&:hover': {
        backgroundColor: "#28a745",
      },
    },
  }))(Button);


  const useStyles = makeStyles((theme) => ({
    margin: {
      margin: theme.spacing(1),
      height:'100px',
      width:'300px',
      textAlign:'center',
    //   boxShadow:'0 0 1px rgba(0,0,0,.125), 0 1px 3px rgba(0,0,0,.2)'
    },
    text: {
        width: '100%',
        maxWidth: 500,
        fontSize: '2.2rem',
        fontWeight:'700',
        margin:'0 0 10px 0',
        whiteSpace:'nowrap',
      },
      textsmall: {
        width: '100%',
        maxWidth: 500,
        fontSize: '12px',
        // fontWeight:'700'
      },
      icon:{
        width: 60,
        height: 60,
        paddingright:0,
        margin:'0 0 0 0',
      },

  }));
  
  const theme = createMuiTheme({
    palette: {
      primary: green,
    },
  });

// import './small-box.scss'

const  OverallStats_View = (props) => {
    const classes = useStyles();
   
    return (
        <div><br/><br/>
        <Container fluid={false}>
         <Row>
            <Col col-lg-4 col-6>
            <ColorButton variant="contained" color="primary" className={classes.margin} startIcon={<ConfirmationNumberIcon className={classes.icon} />}>
                <Container>
                    <Row><Col>
                    <Typography variant="h5" component="h5" gutterBottom className={classes.text}>
                       {parseInt(props.data.Confirmed).toLocaleString()}
                    </Typography>
                    </Col></Row>
                    <Row><Col>
                    <Typography variant="h6" component="h6" gutterBottom className={classes.textsmall}>
                        Total Confirmed Cases
                    </Typography>
                    </Col></Row>
                </Container>
         </ColorButton>
            </Col>
            <Col col-lg-4 col-6>
                <ColorButton1 variant="contained" color="primary" className={classes.margin} startIcon={<EmojiPeopleIcon className={classes.icon} />}>
                <Container>
                    <Row><Col>
                    
                    <Typography variant="h5" component="h5" gutterBottom className={classes.text}>
                        {parseInt(props.data.Recovered).toLocaleString()}
                    </Typography>
                    </Col></Row>
                    <Row><Col>
                    <Typography variant="h6" component="h6" gutterBottom className={classes.textsmall}>
                        Total Recovered Cases
                    </Typography>
                    </Col></Row>
                </Container>
                </ColorButton1>

            </Col>
            <Col col-lg-4 col-6>
                <ColorButton2 variant="contained" color="primary" disableRipple className={classes.margin} startIcon={<AirlineSeatFlatIcon className={classes.icon} />}>
                <Container>
                    <Row><Col>
                    <Typography variant="h5" component="h5" gutterBottom className={classes.text}>
                        {parseInt(props.data.Deceased).toLocaleString()}
                    </Typography>
                    </Col></Row>
                    <Row><Col>
                    <Typography variant="h6" component="h6" gutterBottom className={classes.textsmall}>
                        Total Fatality Cases
                    </Typography>
                    </Col></Row>
                </Container>
                </ColorButton2>

            </Col>
           
                </Row>
         </Container>
         </div>
    )
    }


    const  OverallStats = (props) => {
      const { loading, error, data } = useQuery(query_stats);
    
      if (loading) {
        return <div className='loader_graph'>Loading...</div>;
      }
      if (error) {
        console.error( error);
        return  <div>Error</div>;
      }
      return <div><OverallStats_View  data={data.overall_stats[0]}/></div>
      
    };


    export default OverallStats;