import Typography from '@material-ui/core/Typography';
import React, { useRef, useLayoutEffect,useState } from 'react';
import Link from '@material-ui/core/Link';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import { useQuery } from '@apollo/react-hooks';
import gql from "graphql-tag";
// import CardMedia from '@material-ui/core/CardMedia';
import { red } from '@material-ui/core/colors';
import CardContent from '@material-ui/core/CardContent';
import { makeStyles } from '@material-ui/core/styles';
import Divider from '@material-ui/core/Divider';
// import SimplePaper from './SimplePaper'
import './App.css';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";
import * as am4plugins_timeline from "@amcharts/amcharts4/plugins/timeline";
import * as am4plugins_bullets from "@amcharts/amcharts4/plugins/bullets";
import * as am4plugins_forceDirected from "@amcharts/amcharts4/plugins/forceDirected"; 


const policyindex=  gql`   
query{
    policy_index{
    date,india,telangana
     }
     ,
     country_policies_india{
            start,end,task           
    },
     country_policies_telangana{
            start,end,task           
    },
}`


const useStyles = makeStyles((theme) => ({
    root: {
      maxWidth: '100%',
      flexGrow: 1,
    },header:{
        background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
        borderRadius: 3,
        border: 0,
        color: 'white',
        height: 48,
        padding: '0 30px',
        boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
      },
      headermain:{
        background: 'linear-gradient(45deg, #2196f3 30%, #21cbf3 90%)',
        boxShadow: '0 3px 5px 2px rgba(33, 203, 243, .30)',
        color: 'white',
      },
    media: {
      height: 0,
      paddingTop: '56.25%', // 16:9
    },
    expand: {
      transform: 'rotate(0deg)',
      marginLeft: 'auto',
      transition: theme.transitions.create('transform', {
        duration: theme.transitions.duration.shortest,
      }),
    },
    expandOpen: {
      transform: 'rotate(180deg)',
    },
    avatar: {
      backgroundColor: red[500],
    },
    paper: {
        padding: theme.spacing(0),
        textAlign: 'center',
        color: theme.palette.text.secondary,
        margin: 'auto',
        
      },
      clock:{
        maxWidth: '100%',
        flexGrow: 1,
        textAlign: 'left',
      }
  }));
export default function CountryPolicies() {
    const classes = useStyles();
    const { loading, error, data } = useQuery(policyindex);
    const [state,setState]= useState('Telangana');
    // const [state,setState]= useState('Confirmed');
   
       if (loading) {
         return <div className='loader_graph'>Loading...</div>;
       }
       if (error) {
         console.error(error);
         return  <div>Error</div>;
       }
       const onClickHandler = (btnname) =>{
        setState(btnname)
        // console.log(`btn clicked ${btnname} ${JSON.stringify(data)}`)
      //  return <Test data={data} btnname={state} />
      }
    return (
<div>
   
    <div style={{textAlign:'left'}}>
     <br/><br/>
    <ButtonGroup color="secondary" aria-label="outlined secondary button group" style={{marginBottom:'5px'}}>
        <Button onClick={()=>onClickHandler("Telangana")}>Telangana</Button>
        <Button onClick={()=>onClickHandler("India")}>India</Button>
      </ButtonGroup>
      </div>
<CreateChart data={state==='India'? data.country_policies_india : data.country_policies_telangana} title={state} />
<br/>
<PolicyIndex data ={data.policy_index}/>
</div>
    );
}

am4core.useTheme(am4themes_animated);
function CreateChart(props) {
    const chart = useRef(null);
    const classes = useStyles();
   console.log(JSON.stringify(props.data));
    useLayoutEffect(() => {
        var container = am4core.create("countrypolicies", am4core.Container);
        container.width = am4core.percent(100);
        container.height = am4core.percent(100);
        
        var interfaceColors = new am4core.InterfaceColorSet();
        var colorSet = new am4core.ColorSet();
        
        var chart = container.createChild(am4plugins_timeline.CurveChart);
        container.logo.height =  -15000; 

        chart.data =props.data.reverse()

        let min_act=props.data.reduce(
            (olddt,newdt)=> 
            (new Date(olddt.start) ) < (new Date(newdt.start) )
            ? olddt:newdt)
          
          
            let max_act=props.data.reduce(
              (olddt,newdt)=> 
              (new Date(olddt.end) ) > (new Date(newdt.end))
              ? olddt:newdt)
        console.log(`min_act:${min_act.start}`);
        console.log(`max_act:${max_act.end}`);
        
        chart.dateFormatter.dateFormat = "yyyy-MM-dd";
        chart.dateFormatter.inputDateFormat = "yyyy-MM-dd";
        chart.dy = 75;
        chart.maskBullets = false;
        
        var categoryAxis = chart.yAxes.push(new am4charts.CategoryAxis());
        categoryAxis.dataFields.category = "task";
        categoryAxis.renderer.labels.template.paddingRight = 25;
        categoryAxis.renderer.minGridDistance = 10;
        categoryAxis.renderer.innerRadius = 0;
        categoryAxis.renderer.radius = 100;
        categoryAxis.renderer.grid.template.location = 1;
        
        var dateAxis = chart.xAxes.push(new am4charts.DateAxis());
        dateAxis.renderer.minGridDistance = 70;
        dateAxis.min = new Date(min_act).getTime();
        dateAxis.max = new Date(max_act).getTime();
        
        dateAxis.baseInterval = { count: 1, timeUnit: "month" };
        dateAxis.startLocation = -0.5;
        
        dateAxis.renderer.points = [{ x: -400, y: 0 }, { x: -250, y: 0 }, { x: 0, y: 60 }, { x: 250, y: 0 }, { x: 400, y: 0 }];
        dateAxis.renderer.autoScale = false;
        dateAxis.renderer.polyspline.tensionX = 0.8;
        dateAxis.renderer.tooltipLocation = 0;
        dateAxis.renderer.grid.template.disabled = true;
        dateAxis.renderer.line.strokeDasharray = "1,4";
        dateAxis.renderer.line.strokeOpacity = 0.7;
        dateAxis.tooltip.background.fillOpacity = 0.2;
        dateAxis.tooltip.background.cornerRadius = 5;
        dateAxis.tooltip.label.fill = new am4core.InterfaceColorSet().getFor("alternativeBackground");
        dateAxis.tooltip.label.paddingTop = 7;
        
        var labelTemplate = dateAxis.renderer.labels.template;
        labelTemplate.verticalCenter = "middle";
        labelTemplate.fillOpacity = 0.7;
        labelTemplate.background.fill = interfaceColors.getFor("background");
        labelTemplate.background.fillOpacity = 1;
        labelTemplate.padding(7,7,7,7);
        
        var series = chart.series.push(new am4plugins_timeline.CurveColumnSeries());
        series.columns.template.height = am4core.percent(15);
        series.columns.template.tooltipText = "{categoryX}: [bold]{openDateX}[/] - [bold]{dateX}[/]";
        
        series.dataFields.openDateX = "start";
        series.dataFields.dateX = "end";
        series.dataFields.categoryY = "task";
        series.columns.template.propertyFields.fill = "color"; // get color from data
        series.columns.template.propertyFields.stroke = "color";
        series.columns.template.strokeOpacity = 0;
        
        series.columns.template.adapter.add("fill", function (fill, target) {
           return chart.colors.getIndex(target.dataItem.index * 3);
        })
        
        var flagBullet1 = new am4plugins_bullets.FlagBullet();
        series.bullets.push(flagBullet1);
        flagBullet1.disabled = true;
        flagBullet1.propertyFields.disabled = "bulletf1";
        flagBullet1.locationX = 1;
        flagBullet1.label.text = "start";
        
        var flagBullet2 = new am4plugins_bullets.FlagBullet();
        series.bullets.push(flagBullet2);
        flagBullet2.disabled = true;
        flagBullet2.propertyFields.disabled = "bulletf2";
        flagBullet2.locationX = 0;
        flagBullet2.background.fill = interfaceColors.getFor("background");
        flagBullet2.label.text = "end";
        
        var bullet = new am4charts.CircleBullet();
        series.bullets.push(bullet);
        bullet.circle.radius = 3;
        bullet.circle.strokeOpacity = 0;
        bullet.locationX = 0;
        
        bullet.adapter.add("fill", function (fill, target) {
           return chart.colors.getIndex(target.dataItem.index * 3);
        })
        
        var bullet2 = new am4charts.CircleBullet();
        series.bullets.push(bullet2);
        bullet2.circle.radius = 3;
        bullet2.circle.strokeOpacity = 0;
        bullet2.propertyFields.fill = "color";
        bullet2.locationX = 1;
        
        bullet2.adapter.add("fill", function (fill, target) {
           return chart.colors.getIndex(target.dataItem.index * 3);
        })
        
        chart.scrollbarX = new am4core.Scrollbar();
        chart.scrollbarX.align = "center"
        chart.scrollbarX.width = 800;
        chart.scrollbarX.parent = chart.bottomAxesContainer;
        chart.scrollbarX.dy = - 90;
        chart.scrollbarX.opacity = 0.4;
        
        var cursor = new am4plugins_timeline.CurveCursor();
        chart.cursor = cursor;
        cursor.xAxis = dateAxis;
        cursor.yAxis = categoryAxis;
        cursor.lineY.disabled = true;
        cursor.lineX.strokeDasharray = "1,4";
        cursor.lineX.strokeOpacity = 1;
        
        dateAxis.renderer.tooltipLocation2 = 0;
        categoryAxis.cursorTooltipEnabled = false;
        
        
        /// clock
        var clock = container.createChild(am4charts.GaugeChart);
        clock.toBack();
        
        clock.radius = 120;
        clock.dy = -100;
        clock.startAngle = -90;
        clock.endAngle = 270;
        
        var axis = clock.xAxes.push(new am4charts.ValueAxis());
        axis.min = 0;
        axis.max = 12;
        axis.strictMinMax = true;
        
        axis.renderer.line.strokeWidth = 1;
        axis.renderer.line.strokeOpacity = 0.5;
        axis.renderer.line.strokeDasharray = "1,4";
        axis.renderer.minLabelPosition = 0.05; // hides 0 label
        axis.renderer.inside = true;
        axis.renderer.labels.template.radius = 30;
        axis.renderer.grid.template.disabled = true;
        axis.renderer.ticks.template.length = 12;
        axis.renderer.ticks.template.strokeOpacity = 1;
        
        // serves as a clock face fill
        var range = axis.axisRanges.create();
        range.value = 0;
        range.endValue = 12;
        range.grid.visible = false;
        range.tick.visible = false;
        range.label.visible = false;
        
        var axisFill = range.axisFill;
        
        // hands
        var hourHand = clock.hands.push(new am4charts.ClockHand());
        hourHand.radius = am4core.percent(60);
        hourHand.startWidth = 5;
        hourHand.endWidth = 5;
        hourHand.rotationDirection = "clockWise";
        hourHand.pin.radius = 8;
        hourHand.zIndex = 1;
        
        var minutesHand = clock.hands.push(new am4charts.ClockHand());
        minutesHand.rotationDirection = "clockWise";
        minutesHand.startWidth = 3;
        minutesHand.endWidth = 3;
        minutesHand.radius = am4core.percent(78);
        minutesHand.zIndex = 2;
        
        chart.cursor.events.on("cursorpositionchanged", function (event) {
           var value = dateAxis.positionToValue(event.target.xPosition)
           var date = new Date(value);
           var hours = date.getHours();
           var minutes = date.getMinutes();
           // set hours
           hourHand.showValue(hours + minutes / 60, 0);
           // set minutes
           minutesHand.showValue(12 * minutes/ 60, 0);       
        })
      return () => {
        chart.dispose();
      };
    },[props.btnname,props.data] );
  
    return (
  
    <div>
   <Card className={classes.root}>
  
   <CardHeader className={classes.header}
    // style={{Color: "#000", Background: "blue"}}
          title="Restriction Policies"
          subheader={props.title}
        />
   
   <Divider />
      <CardContent id="countrypolicies" style={{ width: "100%", height: "500px"}}/> 
      </Card>
  
      </div>
    );
  }

  function PolicyIndex(props) {
    const chart = useRef(null);
    const classes = useStyles();
    let itemtitle=''
    console.log(`In malefemale: ${JSON.stringify(props.data)}`)
    if (props.btnname==='Confirmed') {
     itemtitle ="Covid Confirmed Cases Trend"
    } else if(props.btnname==='Recovered'){
     itemtitle="Covid Recovered Cases Trend"
    } else {
     itemtitle="Covid Death Cases Trend"
    }
    useLayoutEffect(() => {
       
var mainContainer = am4core.create("PolicyIndex", am4core.Container);
mainContainer.width = am4core.percent(100);
mainContainer.height = am4core.percent(100);
mainContainer.layout = "horizontal";
mainContainer.logo.height =  -15000; 
var usData = props.data;

var maleChart = mainContainer.createChild(am4charts.XYChart);
maleChart.paddingRight = 0;
// maleChart.data = JSON.parse(JSON.stringify(usData));
maleChart.data=usData
console.log(`JSON.parse(JSON.stringify(usData)) : ${JSON.parse(JSON.stringify(usData))}`)
// Create axes
var maleCategoryAxis = maleChart.yAxes.push(new am4charts.CategoryAxis());
// let maleCategoryAxis = maleChart.yAxes.push(new am4charts.DateAxis());

maleCategoryAxis.dataFields.category = "date";
maleCategoryAxis.renderer.grid.template.location = 0;
maleCategoryAxis.renderer.inversed = false;
maleCategoryAxis.renderer.minGridDistance = 15;

var maleValueAxis = maleChart.xAxes.push(new am4charts.ValueAxis());
maleValueAxis.renderer.inversed = true;
maleValueAxis.min = 0;
maleValueAxis.max = 1;
maleValueAxis.strictMinMax = true;

maleCategoryAxis.dateFormatter = new am4core.DateFormatter();
maleCategoryAxis.dateFormatter.dateFormat  = "d MMM, yyyy";
// maleValueAxis.numberFormatter = new am4core.NumberFormatter();
// maleValueAxis.numberFormatter.numberFormat = "#.#'%'";

// Create series
var maleSeries = maleChart.series.push(new am4charts.ColumnSeries());
maleSeries.dataFields.valueX = "india";
// maleSeries.dataFields.valueXShow = "percent";
// maleSeries.calculatePercent = true;
maleSeries.dataFields.categoryY = "date";
// maleSeries.interpolationDuration = 1000;
maleSeries.columns.template.tooltipText = "India: {valueX}";
//maleSeries.sequencedInterpolation = true;


var femaleChart = mainContainer.createChild(am4charts.XYChart);
femaleChart.paddingLeft = 0;
// femaleChart.data = JSON.parse(JSON.stringify(usData));
femaleChart.data= usData
// Create axes
var femaleCategoryAxis = femaleChart.yAxes.push(new am4charts.CategoryAxis());
femaleCategoryAxis.renderer.opposite = true;
femaleCategoryAxis.dataFields.category = "date";
femaleCategoryAxis.renderer.grid.template.location = 0;
femaleCategoryAxis.renderer.minGridDistance = 15;
femaleCategoryAxis.renderer.inversed=false;
var femaleValueAxis = femaleChart.xAxes.push(new am4charts.ValueAxis());
femaleValueAxis.min = 0;
femaleValueAxis.max = 1;
femaleValueAxis.strictMinMax = true;
// femaleValueAxis.numberFormatter = new am4core.NumberFormatter();
// femaleValueAxis.numberFormatter.numberFormat = "#.#'%'";
femaleValueAxis.renderer.minLabelPosition = 0.01;

// Create series
var femaleSeries = femaleChart.series.push(new am4charts.ColumnSeries());
femaleSeries.dataFields.valueX = "telangana";
// femaleSeries.dataFields.valueXShow = "percent";
// femaleSeries.calculatePercent = true;
femaleSeries.fill = femaleChart.colors.getIndex(4);
femaleSeries.stroke = femaleSeries.fill;
//femaleSeries.sequencedInterpolation = true;
femaleSeries.columns.template.tooltipText = "Telangana: {valueX}";
femaleSeries.dataFields.categoryY = "date";
// femaleSeries.interpolationDuration = 1000;

  
      return () => {
        mainContainer.dispose();
      };
    },[props.data] );
  
    return (
  
    <div>
   <Card className={classes.root}>
  
   <CardHeader className={classes.header}
    // style={{Color: "#000", Background: "blue"}}
          title="Policy Index"
          subheader="Lesser the value better"
        />
   
   <Divider />
      <CardContent  id="PolicyIndex" style={{ width: "100%", height: "500px" }}/> 
      </Card>
  
      </div>
    );
  }
  