// import { Bar } from "react-chartjs-2";
import React,{ useLayoutEffect } from 'react';
import { useQuery } from '@apollo/react-hooks';
import gql from "graphql-tag";
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";
import {Container,Row, Col } from 'reactstrap'
import 'bootstrap/dist/css/bootstrap.css';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import { red } from '@material-ui/core/colors';




const querygraph=  gql`   
    query{
        cases_per_thousand{
            Date,ACTIVE_CASES_PER_1000,NEW_CASES_PER_10000,DEATHS_PER_1000
             }
         }
       `
       
const useStyles = makeStyles((theme) => ({
    root: {
      maxWidth: '100%',
      flexGrow: 0,
    //   maxHeight:'20%'
      
    },
    media: {
      height: 0,
      paddingTop: '0%', // 16:9
    },
    expand: {
      transform: 'rotate(0deg)',
      marginLeft: 'auto',
      transition: theme.transitions.create('transform', {
        duration: theme.transitions.duration.shortest,
      }),
    },
    expandOpen: {
      transform: 'rotate(180deg)',
    },
    avatar: {
      backgroundColor: red[500],
    },
    paper: {
        padding: theme.spacing(2),
        textAlign: 'center',
        color: theme.palette.text.secondary,
        margin: 'auto',
        // background: 'linear-gradient(45deg, #2196F3 30%, #21CBF3 90%)',
        borderRadius: 3,
        border: 0,
        // color: 'white',
        // height: 48,
        // padding: '0 30px',
        boxShadow: '0 3px 5px 2px rgba(33, 203, 243, .3)',
        
      },
  }));


       function Griditem(props){
    
    
        const classes = useStyles();
        
        return (
            <div className={classes.root}>
              <Grid container spacing={3} className={classes.root}
              justify="center"
              >
               
                <Grid item xs={6} sm={2} id='1'>
                {/* <Paper className={classes.paper} ><CreateChart chtname="test1" color="#800080" title="Active Cases per 1K" value={1} /> </Paper> */}
                <SmallBarsViews  chtname="small1" color="#800080"  data={props.data} axis='ACTIVE_CASES_PER_1000'/> 
                </Grid>
                <Grid item xs={6} sm={2} id='2'>
                  {/* <Paper className={classes.paper} ><CreateChart chtname="test2" color="#34a4eb" title="New Cases per 1K" value={0.3}/></Paper> */}
                  <SmallBarsViews chtname="small2" color="#34a4eb" data={props.data} axis='NEW_CASES_PER_10000'/>
                </Grid>
                <Grid item xs={6} sm={2} id='3'>
                  {/* <Paper className={classes.paper} ><CreateChart chtname="test3" color="#3333cc" title="Deaths per 1K" value={0.03}/></Paper> */}
                  <SmallBarsViews chtname="small3" color="#3333cc"  data={props.data} axis='DEATHS_PER_1000'/>
                {/* <SparkLineChart/> */}
                </Grid>
                
              </Grid>
            </div>
          );
    }
    
const   SmallBarsViews =(props)=>{
// const chart = useRef(null);
console.log(`${props.data}`)
useLayoutEffect(() => {
    am4core.useTheme(am4themes_animated);

    var chart = am4core.create(props.chtname, am4charts.XYChart);
    chart.logo.height =  -15000; 
    chart.data =props.data;
    
    chart.padding(0, 0, 0, 0);
    
    var dateAxis = chart.xAxes.push(new am4charts.DateAxis());
    dateAxis.renderer.grid.template.disabled = true;
    dateAxis.renderer.labels.template.disabled = true;
    dateAxis.startLocation = 0.5;
    dateAxis.endLocation = 0.7;
    dateAxis.cursorTooltipEnabled = false;
    
    var valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
    valueAxis.min = 0;
    valueAxis.renderer.grid.template.disabled = true;
    valueAxis.renderer.baseGrid.disabled = true;
    valueAxis.renderer.labels.template.disabled = true;
    valueAxis.cursorTooltipEnabled = false;
    
    chart.cursor = new am4charts.XYCursor();
    chart.cursor.lineY.disabled = true;
    
    var series = chart.series.push(new am4charts.ColumnSeries());
    series.tooltipText = `{Date}: [bold]${props.axis==='ACTIVE_CASES_PER_1000' ? '{ACTIVE_CASES_PER_1000}':props.axis==='NEW_CASES_PER_10000' ? '{NEW_CASES_PER_10000}':props.axis==='DEATHS_PER_1000'?'{DEATHS_PER_1000}':''}`;
    series.dataFields.dateX = "Date";
    series.dataFields.valueY = props.axis;
    series.tensionX = 0.8;
    series.strokeWidth = 2;
    series.stroke = am4core.color(props.color);
    // series.colors = am4core.color("green");
    // render data points as bullets
    var bullet = series.bullets.push(new am4charts.CircleBullet());
    bullet.circle.opacity = 0;
    bullet.circle.propertyFields.opacity = "opacity";
    bullet.circle.radius = 3;
    // bullet.circle.stroke = am4core.color(props.color);

   return () => {
    chart.dispose();
};
}, [props.data,props.chtname]);

return(
    <div id={props.chtname}></div>
)
}



export const  SmallBars =()=>{
    const { loading, error, data } = useQuery(querygraph);
//   console.log(`SmallBars ${JSON.stringify(data.actuals)}`)
    if (loading) {
      return <div className='loader_graph'>Loading...</div>;
    }
    if (error) {
      console.error( error);
      return  <div>Error</div>;
    }return( 
    
    <Griditem data={data.cases_per_thousand}/>

 )
}

export default SmallBars;